import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController } from 'ionic-angular';
import { ApiServiceProvider } from '../../providers/api-service/api-service';
import { Storage } from '@ionic/storage';

/**
 * Generated class for the CircleOfExcellenceListPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-circle-of-excellence-list',
  templateUrl: 'circle-of-excellence-list.html',
})
export class CircleOfExcellenceListPage {
testImage:string='../assets/imgs/teamEvalueserve.png';
  image: any;
  deviceId: any;
  login_token: any;
  employeeId: any;
  loading: any;
  awardList : any;
  
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    private loadingCtrl :LoadingController,
    private storage : Storage,
    private apiServices : ApiServiceProvider
  ) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TopPerformerListPage');
    this.storage.get('employeeId').then(eId => {
      this.employeeId = eId;
      console.log(' employeeId== ', this.employeeId);
      this.storage.get('login_token').then((token) => {
        console.log('showOnBoard value is111==', token);
        this.login_token = token;
        this.storage.get('deviceId').then((dId) => {
          this.deviceId = dId;
          this.getAwardList();
        });
      })
    });
    
  }

  ///// common loader ///////
  commonLoader() {
    this.loading = this.loadingCtrl.create({
      enableBackdropDismiss: true,
      spinner: 'ios-small',
      content: 'Loading data...'
    });
    this.loading.present();
  }

  getAwardList(){
    this.commonLoader();
    let apiKey = {
      "client_id": this.apiServices.clientId,
      "employee_id": this.employeeId,
      "device": this.apiServices.device,
      "device_id": this.deviceId,
      "app_version": this.apiServices.appVersion,
      "flag_type":"awards",
    }
    console.log('key== ', apiKey);
    this.apiServices.getTsopPerformerList(apiKey, this.login_token)
      .subscribe((res) => {
        this.loading.dismiss();
        console.log('communityJoinUnJoinedApi==', res);
        if (res.success == 1) {
          this.awardList = res.data;
        } else {
          this.awardList=[];
          this.image = res.image;
        }
    },(err) => {
      this.loading.dismiss();
      console.log(err);
    });
  }

  gotoawarded(data){
    console.log('nominationid',data);
    this.navCtrl.push('HomeawardsdetailPage',{'nominationData':data});
  }

}



